<template>
  <div class="songlist">
    <div class="back" @click="back"></div>
    <div class="title">推荐歌单</div>
    <ul>
      <li v-for="(item, index) in listy" :key="item.id">
        <router-link
          :to="{
            path: '/Songdetalis',
            query: {
              id: listy[index].id,
            },
          }"
        >
          <img :src="`${item.picUrl}`" />
          <h6>{{ item.playCount | fullNuM }}</h6>
          <p>{{ item.name }}</p>
        </router-link>
      </li>
    </ul>
    <div class="title">精品歌单</div>
    <ul>
      <router-link
        v-for="(item, index) in boutiquelist"
        :key="index"
        tag="li"
        :to="{
          path: '/Songdetalis',
          query: {
            id: boutiquelist[index].id,
          },
        }"
      >
        <img :src="`${item.coverImgUrl}`" />
        <h6>{{ item.playCount | fullNuM }}</h6>
        <p>{{ item.name }}</p>
      </router-link>
    </ul>
    <div class="title">热门歌单</div>
    <ul>
      <router-link
        v-for="(item, index) in popularlist"
        :key="index"
        tag="li"
        :to="{
          path: '/Songdetalis',
          query: {
            id: popularlist[index].id,
          },
        }"
      >
        <img :src="`${item.coverImgUrl}`" />
        <h6>{{ item.playCount | fullNuM }}</h6>
        <p>{{ item.name }}</p>
      </router-link>
    </ul>
  </div>
</template>
<script>
export default {
  data() {
    return {
      listy: [],
      boutiquelist: [],
      popularlist: [],
      id: "",
    };
  },
  filters: {
    fullNuM(value) {
      console.log(value);
      return (value / 10000).toFixed(1) + "�";
    },
  },
  created() {
    //热门歌单
    this.axios("/personalized",{
        params:1
    })
    .then(res =>{
        // console.log(res.data.result);
        this.listy = res.data.result.splice(0,6)
    });
    //获取精品歌单
    this.axios({
      method: "get",
      url: "/top/playlist/highquality",
      params: {
        before: "1503639064232",
        limit: "30",
      },
    }).then((res) => {
      this.boutiquelist = res.data.playlists.splice(0, 6);
      console.log(this.boutiquelist);
    });
    //获取热门歌单
    this.axios({
      method: "get",
      url: "/top/playlist/hot",
    }).then((res) => {
      this.popularlist = res.data.playlists.splice(0, 6);
      console.log(this.popularlist);
    });
  },
  methods:{
    back(){
      this.$router.back()
    }
  }
};
</script>
<style lang="less" scoped>
.songlist {
  width: 100%;
  overflow-y: auto;
  height: 95vh;
  .back{
    position: fixed;
    top: 100px;
    left: 0;
    width: 0.8rem;
    height: 0.8rem;
    background: url(data:image/png;base64,) no-repeat;
    background-size: 0.8rem;
  }
  .title {
    width: 100%;
    height: 20px;
    font-size: 18px;
    color: #000;
    font-weight: 500;
    padding: 0 10px;
    text-align: left;
    margin-left: 1px;
    margin-top: 35px;
    position: relative;
    &::before {
      position: absolute;
      left: 0px;
      content: "";
      top: 50%;
      margin-top: -8px;
      // margin-left:9px;
      width: 2px;
      height: 16px;
      border-left: 2.5px solid #dd001b;
    }
  }
  ul {
    display: flex;
    flex-wrap: wrap;
    li {
      width: 32%;
      height: 175px;
      list-style: none;
      overflow: hidden;
      position: relative;
      margin: 10px 2px;
      &::after {
        content: " ";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 20px;
        z-index: 2;
        background-image: -webkit-linear-gradient(
          top,
          rgba(0, 0, 0, 0.2),
          rgba(0, 0, 0, 0)
        );
        background-image: linear-gradient(
          180deg,
          rgba(0, 0, 0, 0.2),
          rgba(0, 0, 0, 0)
        );
      }

      img {
        width: 100%;
      }

      h6 {
        position: absolute;
        right: 5px;
        top: 2px;
        z-index: 3;
        padding-left: 13px;
        color: #fff;
        font-size: 12px;
        background-position: 0;
        background-repeat: no-repeat;
        background-size: 11px 10px;
        text-shadow: 1px 0 0 rgb(0, 0, 0/15%);
        background-image: url(data:image/svg+xml;base64,);
      }

      p {
        font-weight: 400;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        padding: 6px 2px 0px 6px;
        width: 120px;
        height: 38px;
        text-overflow: ellipsis;
        color: #000;
        font-size: 13px;
        box-sizing: border-box;
      }
    }
  }
}
</style>